'use strict';

layui.define([], function (exports) {
    exports("orderConfig", {
        order_status: function order_status(status) {
            // 订单状态,0新单，1锁单（订单商品未进项，客户选开票，1订单进入人工审核），2有付款，3结单
            var hash = {
                '0': '新单',
                '1': '锁单',
                '2': '部分完成',
                '3': '已完成',
                '100': '已删除',
                '101': '客户申请取消'
            };
            var res = hash[String(status)];
            return res ? res : '无';
        },
        pay_status: function pay_status(status) {
            // 支付状态,0未付款，1部分支付，2全部支付
            var hash = {
                '0': '未付款',
                '1': '部分支付',
                '2': '全部支付'
            };
            var res = hash[String(status)];
            return res ? res : '无';
        },
        ship_status: function ship_status(status) {
            // 运输状态，0待发货，1已部分发货，2已全部发货，3已部分收货，4已全部收货，100初始值
            var hash = {
                '0': '待发货',
                '1': '已部分发货',
                '2': '待收货',
                '3': '已部分收货',
                '4': '已全部收货',
                '100': '初始值'
            };
            var res = hash[String(status)];
            return res ? res : '无';
        },
        ship_type: function ship_type(status) {
            // 1快递，2物流，3自取，4送货
            var hash = {
                '1': '快递',
                '2': '物流',
                '3': '自取',
                '4': '送货'
            };
            var res = hash[String(status)];
            return res ? res : '无';
        },
        pay_type: function pay_type(status) {
            // 支付方式（1在线支付，2账期支付，3快递代收，4面对面付款，5银行转账，6线下支付）
            var hash = {
                '1': '在线支付',
                '2': '账期支付',
                '3': '快递代收',
                '4': '面对面付款',
                '5': '银行转账',
                '6': '线下支付'
            };
            var res = hash[String(status)];
            return res ? res : '无';
        }
    });
});